import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import './styles/styles.css'
import { Modal } from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

class Album extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = 
        {
            teamStats: [],
            showTeamPlayerModal: false,
            teamPlayerDetails: {},
        };
        
    }
    handleOpen = () => {
        this.setState({
            showTeamPlayerModal: true
        });
    };

    handleClose = () => {
        this.setState({
            showTeamPlayerModal: false
        });
    };
    getTeamPlayerDetails = teamName => {
        const url = 'https://vast-earth-90578.herokuapp.com/getTeamsDetails/'+teamName;
        fetch(encodeURI(url))
        .then(response => response.json())
        .then(data => {
          // Work with JSON data here
          console.log(data);
            this.setState({
            showTeamPlayerModal: true
            });
        });
    }
    componentDidMount() {
      // this.state.loading: true;
      fetch('https://vast-earth-90578.herokuapp.com/getTeamsStats')
      .then(response => response.json())
      .then(data => {
        // Work with JSON data here
        console.log(data);
        let teamStats = data.map(teamStat => {
            return (<Grid item key={teamStat} xs={12} sm={6} md={4}>
              <Card className={this.props.classes.card}>
                <CardContent className={this.props.classes.cardContent}>
                  <Typography gutterBottom variant="h5" component="h2">
                    {teamStat.team_name}
                  </Typography>
                  <Typography>
                    Player Count: {teamStat.player_count || 'NA'}
                  </Typography>
                  <Typography>
                    Average Player Rating: {teamStat.player_rating ? parseInt(teamStat.player_rating) : 'NA'}
                  </Typography>
                </CardContent>
                <CardActions>
                  <Button size="small" color="primary" onClick={() => {this.getTeamPlayerDetails(teamStat.team_name)}}>
                    View
                  </Button>
                </CardActions>
              </Card>
            </Grid>)
        });
          this.setState({
            teamStats: teamStats
          });
      })
      .catch(err => {
        console.log('error occurred',err);
      })
    }
  render () {
  return (
    <React.Fragment>
      <CssBaseline />
      <AppBar position="relative">
        <Toolbar>
          {/* <CameraIcon className={this.props.classes.icon} /> */}
          <Typography variant="h6" color="inherit" noWrap>
            IPL Auction
          </Typography>
        </Toolbar>
      </AppBar>
      <main>
        {/* Hero unit */}
        {/* <div className={this.props.classes.heroContent}>
          <Container maxWidth="sm">
            <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
              Album layout
            </Typography>
            <Typography variant="h5" align="center" color="textSecondary" paragraph>
              Something short and leading about the collection belowâ€”its contents, the creator, etc.
              Make it short and sweet, but not too short so folks don&apos;t simply skip over it
              entirely.
            </Typography>
            <div className={this.props.classes.heroButtons}>
              <Grid container spacing={2} justify="center">
                <Grid item>
                  <Button variant="contained" color="primary">
                    Main call to action
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="outlined" color="primary">
                    Secondary action
                  </Button>
                </Grid>
              </Grid>
            </div>
          </Container>
        </div> */}
        <Container className={this.props.classes.cardGrid} maxWidth="md">
          {/* End hero unit */}
          <Grid container spacing={4}>
            {this.state.teamStats}
          </Grid>
        </Container>
        <Container>
        <Modal
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
                open={this.state.showTeamPlayerModal}
                onClose={this.handleClose}
            >
            <div style={this.props.modalStyle} className={this.props.classes.paper}>
                <TableContainer component={Paper}>
      <Table className={this.props.classes.table} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Player Name</TableCell>
            <TableCell align="center">Type</TableCell>
            <TableCell align="center">Role</TableCell>
            <TableCell align="center">Origin</TableCell>
            <TableCell align="center">Rating</TableCell>
            <TableCell align="center">Price</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
        <TableRow>
            <TableCell align="left">Sample</TableCell>
              <TableCell component="th" scope="row">
                Sample
              </TableCell>
              <TableCell align="right">Sample</TableCell>
              <TableCell align="right">Sample</TableCell>
              <TableCell align="right">Sample</TableCell>
              <TableCell align="right">Sample</TableCell>
              <TableCell align="right">Price</TableCell>
            </TableRow>
        </TableBody>
        </Table>
        </TableContainer>
                </div>
            </Modal> 
            </Container>
      </main>
      {/* Footer */}
      <footer className={this.props.classes.footer}>
        <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
        With &lt;3
        </Typography>
        <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
        Fun @ Work Team
        </Typography>
      </footer>
      {/* End footer */}
      
    </React.Fragment>
  );
    }
}
export default Album;
